@-webkit-keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes fadeIn {
  from { opacity:0;}
  to {opacity:1;}
}

@keyframes fadeIn {
  from { opacity:0;}
  to {opacity:1;}
} 

@-webkit-keyframes slide {
  from {left:10cm;}
  to {left:0cm;}
} 

@keyframes slide {
  from {left:10cm;}
  to {left:0cm;}
}


@-webkit-keyframes type {
  0% {
    width: 0;
  }

}


@keyframes type {
  0% {
    width: 0;
  }

}
@-webkit-keyframes caret { 
  50% { 
    box-shadow: .6em 0 0 transparent;
  }
}
@keyframes caret { 
  50% { 
    box-shadow: .6em 0 0 transparent;
  }
}
@-webkit-keyframes slides{
  from {right:10cm;}
