.lii {
	position: relative;
	float: left;
	width: 204px;
	height: 204px;
	margin: 5px;
	padding: 5px;
  z-index: 0;
}

/* Make sure z-index is higher on hover */
/* Ensure that hover image overlapped the others */
.lii:hover {
	z-index: 5;
}

/* Image is position nicely under li */
.imj {
	position: absolute;
	left: 0;
	top: 0;
	border: 1px solid #dddddd;
	padding: 5px;
	width: 204px;
	height: 204px;
	background: #f0f0f0;
}

.imj:hover {
	width: 290px;
	height: 290px;
	margin-top: -160px;
	margin-left: -160px;
	top: 65%;
	left: 65%;
}

